blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

main {
  overflow: clip;
}

/*end Reset default assets*/
html {
  scroll-behavior: smooth;
}

[id] {
  scroll-margin-top: 80px;
}
@media (min-width: 1200px) {
  [id] {
    scroll-margin-top: 159px;
  }
}

body.logged-in {
  overflow-x: clip;
}
body.logged-in header.fixed-top {
  margin-top: 0 !important;
}
body.logged-in .admin-view {
  padding: 5px;
  background-color: #000;
  color: #fff;
  display: inline-flex;
  border-radius: 2px;
  opacity: 0.4;
  margin: 0 auto 30px;
  position: relative;
}
body.logged-in .admin-view::after {
  content: "[only admin can view this button]";
  display: inline-block;
  font-size: 11px;
  margin-left: 5px;
}

@media (max-width: 600px) {
  body.logged-in.admin-bar #wpadminbar {
    position: fixed;
  }
}

img {
  max-width: 100%;
  height: auto;
}
img[src$=".svg"] {
  width: auto;
}
img::selection {
  background-color: transparent;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

body.logged-in .empty-link {
  text-decoration: none;
  position: relative;
  box-shadow: 0 0 3px 0 rgb(255, 5, 0);
}

a.empty-link[aria-disabled=true] {
  cursor: not-allowed;
}

div[class*=row-gap-] {
  display: flex;
  flex-direction: column;
}

.font-bold {
  font-weight: bold;
}

.row-gap-1 {
  row-gap: 10px;
}

.row-gap-2 {
  row-gap: 20px;
}

.row-gap-3 {
  row-gap: 30px;
}

.row-gap-4 {
  row-gap: 40px;
}

.row-gap-5 {
  row-gap: 50px;
}

/*Limit Excerpt Text*/
.line-clamp-6 {
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-5 {
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-4 {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-1 {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.stretched-link-custom {
  height: 0;
  width: 0;
  opacity: 0;
  display: block;
}

.alignfull {
  width: 100vw;
  position: relative;
  left: calc(-50vw + 50%);
}

.aligncenter {
  display: flex !important;
  justify-content: center;
  flex-wrap: wrap;
}

.visually-hidden {
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}
.visually-hidden:not(caption) {
  position: absolute !important;
}

.visually-hidden-focusable:not(:focus):not(:focus-within) {
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}
.visually-hidden-focusable:not(:focus):not(:focus-within):not(caption) {
  position: absolute !important;
}

body.admin-bar .header {
  top: 32px;
}
@media (max-width: 782px) {
  body.admin-bar .header {
    top: 46px;
  }
}

body.logged-in .user-logged {
  display: none !important;
}

body.logged-in .not-logged {
  display: block;
}

body:not(.logged-in) .not-logged {
  display: none !important;
}

