.container {
  max-width: 100%;
  padding: 0 1.5rem;
  margin: 0 auto;
}
@media (min-width: 576px) {
  .container {
    width: 575px;
    padding: 0 60px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 992px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
@media (min-width: 1400px) {
  .container {
    width: 1440px;
  }
}

.custom-container {
  max-width: 100%;
  margin: 0 auto;
}
.custom-container:not(.container .custom-container:not(.container .alignfull .custom-container)) {
  padding: 0 1rem;
}
@media (min-width: 576px) {
  .custom-container {
    width: 575px;
  }
  .custom-container:not(.container .custom-container:not(.container .alignfull .custom-container)) {
    padding: 0 1.5rem;
  }
}
@media (min-width: 768px) {
  .custom-container {
    width: 768px;
  }
}
@media (min-width: 992px) {
  .custom-container {
    width: 992px;
  }
  .custom-container--small {
    width: 870px;
  }
  .custom-container--small:not(.container .custom-container:not(.container .alignfull .custom-container)) {
    padding: 0;
  }
}
@media (min-width: 992px) and (max-width: 1199.98px) {
  .custom-container {
    padding: 0 0.46875rem;
  }
}
@media (min-width: 1200px) {
  .custom-container--medium {
    width: 1095px;
  }
  .custom-container--medium:not(.container .custom-container:not(.container .alignfull .custom-container)) {
    padding: 0;
  }
}